@media screen and (max-width: 960px) and (min-width: 391px) {
  html {
    font-size: 16px;
  }

  .wrap {
    padding: 0 15px;
  }
}

@media screen and (max-width: 390px) {
  html {
    font-size: 14px;
  }

  .wrap {
    padding: 0 10px;
  }

  main#main {
    margin: 30px 0;

    & section[class] {
      margin: 50px 0;
    }

    & section[class]:first-child {
      margin-top: 0;
    }
  }
}